
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1
}
ol,ul{list-style:none
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
.margin-top-small{margin-top:20px
}
.margin-top-medium{margin-top:35px
}
.margin-top-big{margin-top:64px
}
.margin-right-small{margin-right:20px
}
.margin-right-medium{margin-right:35px
}
.margin-right-big{margin-right:64px
}
.margin-bottom-small{margin-bottom:20px
}
.margin-bottom-medium{margin-bottom:35px
}
.margin-bottom-big{margin-bottom:64px
}
.margin-left-small{margin-left:20px
}
.margin-left-medium{margin-left:35px
}
.margin-left-big{margin-left:64px
}
.padding-top-small{padding-top:20px
}
.padding-top-medium{padding-top:35px
}
.padding-top-big{padding-top:64px
}
.padding-right-small{padding-right:20px
}
.padding-right-medium{padding-right:35px
}
.padding-right-big{padding-right:64px
}
.padding-bottom-small{padding-bottom:20px
}
.padding-bottom-medium{padding-bottom:35px
}
.padding-bottom-big{padding-bottom:64px
}
.padding-left-small{padding-left:20px
}
.padding-left-medium{padding-left:35px
}
.padding-left-big{padding-left:64px
}
.left{float:left
}
.right{float:right
}
.clr{clear:both
}
@font-face{font-family:"Apple";src:url("/static/fonts/apple.ttf")
}
@font-face{font-family:"Disposable";src:url("/static/fonts/disposable.ttf")
}
body{background-image:url("./assets/bg_trame.png");background-size:3px;font-family:"Disposable";font-size:16px;cursor:url("/static/cursor.png")
}
.center{display:-ms-flexbox;display:flex;margin:30px;-ms-flex-align:start;align-items:start
}
.container{max-width:1000px;margin:auto;margin-top:100px
}
input{outline:none
}
input[type='submit'],a{cursor:pointer
}
h1,h2,h3,h4,h5,h6{font-size:16px;text-transform:uppercase
}



