
.chat__body{width:calc(100% - 300px);margin-left:32px;float:left;border:4px solid black;position:relative;background-color:#EBEBEB;min-height:500px;height:calc(100vh - 200px)
}
.chat__title{font-size:25px;padding:20px;border-bottom:4px solid black
}
.chat__actions{display:block;width:50px;margin-left:35px
}
.chat__action{height:40px;width:40px;border:4px solid black;margin-bottom:30px;background-position:center;background-repeat:no-repeat;background-size:60%;display:block;background-color:#EBEBEB;box-shadow:0 0 0 black;transition:.2s;transform:translateX(0) translateY(0) scale(1)
}
.chat__action:hover{box-shadow:4px 4px 0 black;transform:translateX(-3px) translateY(-3px) scale(1.05)
}
.chat__action-emoji{background-image:url("/static/icons/Smiley.svg")
}
.chat__action-wizz{background-image:url("/static/icons/Wizz.svg")
}
.action__disconnect{width:100%;background:#EBEBEB;display:block;border:4px solid black;margin-top:35px;padding:15px;box-sizing:border-box;text-align:center;text-transform:uppercase;font-size:25px
}
.chat.wizz .chat__body{animation-name:wizz;animation-duration:.1s;animation-iteration-count:infinite
}
@keyframes wizz{
0%{transform:translateX(0px)
}
25%{transform:translateX(10px)
}
75%{transform:translateX(-10px)
}
100%{transform:translateX(0)
}
}



